@import "../../../styles/vars.scss";
@import "../../../styles/mixins.scss";
@import "../../../styles/placeholders.scss";

.paymentHero {
  @include block-model(40px 0 187px, none, 0, 0);
  background: url("../../../images/payment-page/payment-hero.png") bottom / contain no-repeat
    $light-color;

  @media (min-display-pixe-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background: url("../../../images/payment-page/payment-hero.png") bottom / contain no-repeat
      $light-color;
  }

  @media screen and (min-width: 1290px) {
    background: none;
  }

  &__container {
    @media screen and (min-width: 1290px) {
      position: relative;

      display: flex;
      flex-direction: column;
      align-items: flex-start;
      @include size(1290px, 454px);

      background: url("../../../images/payment-page/payment-hero.png") right / contain
        no-repeat #ffffff;

      @media (min-display-pixe-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background: url("../../../images/payment-page/payment-hero.png") center / contain
          no-repeat #ffffff;
      }
    }
  }

  &__box {
    @media screen and (min-width: 1290px) {
      position: absolute;
      top: 50%;
      left: 50px;

      transform: translate(0, -50%);
    }
  }

  &__title {
    text-align: center;
    @include font($dark-color, 1.33, 30px, 600);

    @media screen and (min-width: 1290px) {
      text-align: left;
      @include font($dark-color, 1.33, 40px, 600);
    }
  }

  &__subtitle {
    text-align: center;
    @include block-model(0, none, 0, 30px 0 30px);
    @include font($dark-color, 1.4, 20px, 600);

    @media screen and (min-width: 1290px) {
      max-width: 450px;
      text-align: left;
      @include font($dark-color, 1.4, 26px, 600);
    }
  }

  &__text {
    text-align: center;
    @include font($dark-color, 1.87, 16px, 400);

    @media screen and (min-width: 1290px) {
      max-width: 450px;
      text-align: left;
      @include font($dark-color, 1.87, 20px, 400);
    }
  }
}