.reviews {
  background-color: #e9e9e9;
  padding: 70px 0;
  position: relative;
  overflow: hidden;

  &__container {
    display: flex;
    flex-direction: column;
    position: relative;
  }

  &__containerReviews {
    display: flex;
    flex-direction: row;
    gap: 30px;
    margin-bottom: 30px;
  }

  &__content {
    flex: 1;
  }

  &__content:nth-child(1) {
    z-index: 999;
    background-color: #e9e9e9;
  }

  &__iconSvg {
    width: 60px;
    height: 60px;
    fill: #ffbc57;
  }

  &__title {
    color: var(--Theme-Dark, #2b2929);
    font-family: "Proxima Nova";
    font-size: 40px;
    font-style: normal;
    font-weight: 600;
    line-height: 48px;
    padding-bottom: 40px;
    text-align: left;
  }

  &__logoSofa {
    background-color: white;
    width: 85px;
    height: 85px;
    border-radius: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
  }

  &__name {
    color: var(--Theme-Dark, #2b2929);
    font-family: "Proxima Nova";
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px;
    padding-left: 20px;
    text-align: left;
  }

  &__date {
    color: var(--Theme-Grey, #535353);
    text-align: right;
    font-family: "Proxima Nova";
    font-size: 17px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    padding-left: 80px;
  }

  &__text {
    color: var(--Theme-Grey, #535353);
    font-family: "Proxima Nova";
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px;
    width: 370px;
    height: 150px;

    a {
      color: var(--Theme-Yellow, #ffbc57);
      text-decoration: underline;
    }

    strong {
      font-weight: 600;
    }
  }

  &__rate {
    color: #f90;
    font-family: "Proxima Nova";
    font-size: 19px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px;
    margin-right: 10px;
  }

  &__starSvg {
    width: 24px;
    height: 24px;
    fill: #ff9900;
  }

  &__reviews {
    color: var(--Theme-Grey, #535353);
    font-family: "Proxima Nova";
    font-size: 13px;
    font-style: normal;
    font-weight: 300;
    line-height: 22px;
  }

  &__info {
    display: flex;
    flex-direction: column;
    margin-left: 110px;
    width: 410px;
    height: 150px;
  }

  &__starsContainer {
    flex-direction: row;
    margin-top: 10px;
  }

  &__stars {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  &__star {
    width: 24px;
    height: 24px;
    fill: #ffbc57;
  }

  &__allLink {
    display: flex;
    align-items: center;
    transition: all 0.3s ease-in-out;
  }

  &__buttonContainer {
    display: flex;
    flex-direction: row;
    gap: 30px;
    padding-top: 40px;
  }

  &__allLink:hover {
    color: #ffbc57;
    fill: #ffbc57;
  }

  &__allLink:hover svg {
    fill: #ffbc57;
  }

  &__write {
    border-radius: 8px;
    background: var(--Theme-Yellow, #ffbc57);
    width: 220px;
    height: 50px;
    padding: 8px 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    text-align: center;
    font-family: "Proxima Nova";
    font-size: 19px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px;
    transition: all 0.3s ease-in-out;
  }

  &__write:hover {
    background-color: #535353;
  }

  &__writeSvg {
    fill: white;
    width: 24px;
    height: 24px;
    margin-right: 15px;
  }

  &__all-link {
    color: var(--Theme-Grey, #535353);
    text-align: center;
    font-family: "Proxima Nova";
    font-size: 19px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px;

    display: flex;
    align-items: center;
    transition: all 0.3s ease-in-out;
  }

  &__allLink svg {
    width: 24px;
    height: 24px;
    fill: #535353;
    margin-right: 15px;
    transition: all 0.3s ease-in-out;
  }

  &__list {
    display: flex;
    flex-direction: row;
    gap: 30px;
    overflow: hidden;
    width: 100%;
    transition: transform 0.3s ease;
  }

  &__item {
    width: 390px;
    padding: 30px;
    height: 310px;
    flex: 0 0 auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 30px;
    border-radius: 8px;
    border: 1px solid #e3e2e2;
    background: #fff;
    box-shadow: -3px 4px 15px 0px rgba(0, 0, 0, 0.06);
    transition: 0.2s ease-in-out all;
    margin-left: 10px;

    &:hover {
      transform: scale(1.01) translateY(-20px);
      box-shadow: 0 4px 15px -5px rgba(0,0,0,0.3);
      -webkit-box-shadow: 0 4px 15px -5px rgba(0,0,0,0.3);
      -moz-box-shadow: 0 4px 15px -5px rgba(0,0,0,0.3);
      border-radius: 10px;
    }

    & > div:nth-child(1) {
      display: flex;
      flex-direction: row;
    }

    & > div > div:nth-child(1) {
      border-color: #ffbc57;
      width: 50px;
      height: 50px;
      border-radius: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-style: solid;
      border-width: 1px;
    }
  }

  &__imgContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
    margin-top: 20px;

    picture {
      width: 50px;
      height: 50px;
      border-radius: 8px;
      overflow: hidden;
    }
  }

  &__img {
    width: 50px;
    height: 50px;
    border-radius: 50px;
  }

  &__scroll {
    width: 6px;
    height: 150px;
  }

  &__shop {
    color: var(--Theme-Dark, #2b2929);
    font-family: "Proxima Nova";
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px;
    width: 80%;
    text-align: left;
  }

  &__starsList {
    padding-left: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  &__bedName {
    color: var(--Theme-Grey, #535353);
    font-family: "Proxima Nova";
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px;
  }

  &__list2 {
    display: flex;
    gap: 10px;
    justify-content: center;
    margin-top: 20px;
  }

  &__empty {
    & > svg {
      width: 15px;
      height: 15px;
      border: 2px solid #b1b1b1;
      border-radius: 100%;
    }

    &:nth-child(3) > svg {
      border-color: white;
    }
  }

  &__sliderContainer {
    width: 100%;
    max-width: 695px;
    margin: 0 auto;
    padding: 0 20px;
  }

  &__customSlider {
    -webkit-appearance: none;
    appearance: none;
    width: 100%;
    height: 7px;
    background: #d3d3d3;
    outline: none;
    opacity: 0.7;
    transition: opacity 0.2s;
    border-radius: 5px;

    &::-webkit-slider-thumb {
      -webkit-appearance: none;
      appearance: none;
      width: 60px;
      height: 60px;
      background-color: white;
      background-repeat: no-repeat;
      background-position: center;
      background-size: 45%;
      border-radius: 50%;
      cursor: pointer;
      transform: translateY(0);
      box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
    }

    &::-moz-range-thumb {
      width: 60px;
      height: 60px;
      background-color: white;
      border: 0;
      border-radius: 50%;
      cursor: pointer;
      box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
    }
  }

  &__actions {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  &__likeButton {
    display: flex;
    align-items: center;
    gap: 8px;
    background: none;
    border: none;
    padding: 8px;
    cursor: pointer;
    transition: all 0.3s ease;
    color: var(--Theme-Grey, #535353);
    font-family: "Proxima Nova";
    font-size: 14px;

    &:hover {
      color: var(--Theme-Yellow, #ffbc57);
    }

    svg {
      width: 20px;
      height: 20px;
      fill: currentColor;
      transition: all 0.3s ease;
    }

    &.liked {
      color: var(--Theme-Yellow, #ffbc57);
    }
  }

  &__likeCount {
    font-weight: 600;
    min-width: 20px;
    text-align: center;
  }

  &__deleteButton {
    background-color: transparent;
    margin-left: 20px;
    padding: 8px;
    border: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.button-container {
  display: flex;
  flex-direction: row;
  gap: 30px;
  padding-top: 40px;
}

.img-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
}

@media screen and (min-width: 320px) {
  .reviews {
    &__title {
      font-size: 40px;
    }

    &__list {
      flex-direction: column;
      display: flex;
      align-items: center;
      width: 320px;
    }

    &__item {
      width: 250px;
      height: 390px;
      margin-top: 40px;
      margin-bottom: 30px;
      display: flex;
    }

    &__text {
      text-align: left;
      width: 280px;
      height: 40px;
    }

    &__item:nth-child(2),
    &__item:nth-child(3) {
      display: none;
    }

    &__empty > svg {
      width: 15px;
      height: 15px;
      border: 2px solid #b1b1b1;
      border-radius: 100%;
    }

    &__list2 {
      display: flex;
      gap: 10px;
      justify-content: center;
      margin-top: 20px;
    }

    &__empty:nth-child(3) > svg {
      border-color: white;
    }
  }

  .container-reviews {
    flex-direction: column;
  }

  .custom-slider,
  .slider-container {
    display: none;
  }
}

@media screen and (min-width: 410px) {
  .reviews {
    &__title {
      font-size: 40px;
    }

    &__list {
      flex-direction: column;
      width: 410px;
    }

    &__item {
      width: 450px;
      height: 370px;
      margin-top: 40px;
      margin-bottom: 30px;
      border-radius: 20px;
      display: flex;
    }

    &__text {
      width: 100%;
      height: 150px;
    }

    &__item:nth-child(2),
    &__item:nth-child(3) {
      display: none;
    }

    &__empty > svg {
      width: 15px;
      height: 15px;
      border: 2px solid #b1b1b1;
      border-radius: 100%;
    }

    &__list2 {
      display: flex;
      gap: 10px;
      justify-content: center;
      margin-top: 20px;
    }

    &__empty:nth-child(3) > svg {
      border-color: white;
    }
  }

  .container-reviews {
    flex-direction: column;
  }
}

@media screen and (min-width: 1290px) {
  .reviews {
    &__title {
      font-size: 40px;
    }

    &__list {
      flex-direction: row;
      width: 1500px;
    }

    &__item {
      display: flex;
      width: 450px;
      height: 370px;
      margin-top: 40px;
      margin-bottom: 30px;
    }

    &__item:nth-child(2),
    &__item:nth-child(3) {
      display: flex;
    }

    &__list2 {
      display: none;
    }

    &__empty:nth-child(3) > svg {
      border-color: white;
    }
  }

  .custom-slider,
  .slider-container {
    display: block;
  }

  .container-reviews {
    flex-direction: row;
  }
}

.slider-container {
  width: 695px;
  display: flex;
  justify-content: end;
  margin-left: auto;
}

.custom-slider {
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  height: 7px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  transition: opacity 0.2s;
  border-radius: 5px;
}

.custom-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 60px;
  height: 60px;
  background-color: white;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 45%;
  color: black;
  border-radius: 50%;
  cursor: pointer;
  transform: translateY(0);
}

.custom-slider::-moz-range-thumb {
  width: 20px;
  height: 20px;
  background-color: white;
  border: 0;
  border-radius: 50%;
  cursor: pointer;
}
