.description {
  position: relative;
  padding-top: 50px;
  padding-bottom: 50px;
  background-color: var(--Theme-Light, #f8f8f8);
  &__container {
    @media screen and (min-width: 1290px) {
      display: flex;
      justify-content: space-between;
    }
  }

  &__content {
    @media screen and (min-width: 1290px) {
      width: 850px;
    }
  }

  &__title {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-shrink: 0;
    color: rgba(43, 41, 41, 1);
    font-size: 30px;
    font-weight: 600;
    line-height: 1.33;
    margin-bottom: 30px;
    text-align: left;
  }

  &__text {
    color: rgba(43, 41, 41, 1);

    font-size: 16px;
    font-weight: 400;
    line-height: 1.87;
    margin-bottom: 30px;
    text-align: left;
  }

  // &__image {
  //   width: 410px;
  //   height: 275px;

  //   @media screen and (min-width: 1290px) {
  //     width: 850px;
  //     height: auto;
  //   }
  // }
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 17px;
  }
  &__img {
    width: 178px;
    user-select: none;
  }
}

.descriptionForm {
  &__bug {
    height: 600px;
    padding: 30px;
    border-radius: 8px;
    background-color: #fff;
    display: none;

    @media screen and (min-width: 1290px) {
      display: block;
    }
  }

  &__title {
    color: rgba(43, 41, 41, 1);

    margin-left: auto;
    margin-right: auto;
    font-size: 30px;
    font-weight: 600;
    line-height: 1.33;
    margin-bottom: 30px;
    width: 286px;
  }

  &__input {
    display: flex;
    padding-left: 20px;
    align-items: flex-start;
    gap: 10px;
    border-radius: 8px;
    border: 1px solid rgba(177, 177, 177, 1);
    background: #fff;
    height: 50px;
    margin-bottom: 20px;
    width: 350px;
  }

  &__input::placeholder {
    color: rgba(177, 177, 177, 1);

    font-size: 16px;
    font-weight: 400;
    line-height: 1.87;
    height: 30px;
  }

  &__inputReview {
    flex-shrink: 0;
    border-radius: 8px;
    border: 1px solid rgba(177, 177, 177, 1);
    background: #fff;
    padding-top: 25px;
    padding-bottom: 100px;
  }

  &__text {
    color: rgba(177, 177, 177, 1);
    text-align: center;

    font-size: 19px;
    font-weight: 600;
    line-height: 1.26;
  }

  &__list {
    display: flex;
    justify-content: center;
    margin-top: 10px;
  }

  &__button {
    background-color: transparent;
    padding: 0;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover {
      transform: scale(1.03);
    }
  }

  &__svg {
    width: auto;
    height: auto;
  }

  &__item {
    width: 40px;
    height: 40px;
  }

  &__btn {
    display: flex;
    width: 350px;
    height: 50px;
    padding: 8px 20px;
    justify-content: center;
    align-items: center;
    gap: 15px;
    flex-shrink: 0;
    border-radius: 8px;
    background-color: #feb74b;
    color: #fff;
    text-align: center;

    font-size: 19px;
    font-weight: 600;
    line-height: 1.26;
    margin-top: 30px;
    margin-left: auto;
    margin-right: auto;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
      transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover {
      background-color: #feb74b;
      transform: scale(1.02);
    }
  }
}
.imgBackdrop {
  position: fixed;
  top: 50%;
  left: 50%;  
  transform: translate(-50%, -50%);
  z-index: 2;

  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.407);
  backdrop-filter: blur(5px);

  display: none;
}
.imgModal {
  position: absolute;
  top: 50%;
  left: 50%;  
  transform: translate(-50%, -50%);
  background-color: rgb(66, 66, 66);
  padding: 3px;

  border: solid rgb(66, 66, 66) 3px;
  border-radius: 30px;
  overflow: hidden;

  & img {
    width: 400px;
    transform: scale(1.05);
    border-radius: 30px;
    user-select: none;
    pointer-events: none;
  }
}

.requiredModal {
  position: fixed;
  transform: translateX(100%);
  top: 60px;
  right: 0;
  background-color: var(--Theme-Light, #f8f8f8);
  width: 300px;
  height: 100px;
  border-radius: 30px;
  border: solid #939292 2px;
  display: flex;
  align-items: center;
  justify-content: center;
}
